<!--require('../../components/my-header/js/swiper.min.js');-->
<template>

    <div style="height: 100%;">
        <MyHeader></MyHeader>
        <!-- 轮播图开始 -->
        <div style="height: 1.28rem"></div>
        <div style="height: calc(100% - 2.56rem );">
            <van-swipe :autoplay="3000" indicator-color="white">
                <van-swipe-item><img src="../../components/my-header/img/index/cake.jpg" alt=""></van-swipe-item>
                <van-swipe-item><img src="../../components/my-header/img/index/cake2.jpg" alt=""></van-swipe-item>
                <van-swipe-item><img src="../../components/my-header/img/index/cake3.jpg" alt=""></van-swipe-item>
                <van-swipe-item><img src="../../components/my-header/img/index/cake4.jpg" alt=""></van-swipe-item>
            </van-swipe>
            <!--轮播图接收-->
            <!-- 金刚区开始 -->
            <div class="diamond">
                <a href="html/生日蛋糕.html" class="diamond-content">
                    <div class="img-box">
                        <img src="../../components/my-header/img/index/trophy.png" alt="">
                    </div>
                    <span>生日蛋糕</span>
                </a>
                <a href="html/下午茶.html" class="diamond-content">
                    <div class="img-box2">
                        <img src="../../components/my-header/img/index/trophy1.png" alt="">
                    </div>
                    <span>下午茶</span>
                </a>
                <a href="html/儿童蛋糕.html" class="diamond-content">
                    <div class="img-box3">
                        <img src="../../components/my-header/img/index/trophy2.png" alt="">
                    </div>
                    <span>儿童蛋糕</span>
                </a>
                <a href="html/新品上市.html" class="diamond-content">
                    <div class="img-box4">
                        <img src="../../components/my-header/img/index/trophy3.png" alt="">
                    </div>
                    <span>新品上市</span>
                </a>
            </div>
            <!-- 金刚区结束 -->
            <!-- 商品详情区开始 -->




            <div class="bp item">
                <!-- 品牌特卖 -->
                <div class="brand">
                    <div class="brand-title">
                        <span>企业定制礼盒</span>
                        <i class="iconfont icon-zuo"></i>
                    </div>
                    <div class="pic-box">
                        <div class="pic">
                            <img src="../../components/my-header/img/index/dingzhi1.png" alt="">
                        </div>
                        <div class="pic">
                            <img src="../../components/my-header/img/index/dingzhi2.png" alt="">
                        </div>
                        <div class="pic">
                            <img src="../../components/my-header/img/index/dingzhi3.png" alt="">
                        </div>
                    </div>
                </div>

                <div class="product" v-for="cate in cateGoods" :key="cate.id">
                    <div class="product-title">
                        <router-link tag="span" :to="{name:'list',query:{id:cate.id}}">{{cate.cname}}</router-link>
                        <i class="iconfont icon-zuo"></i>
                    </div>
                    <div class="product-banner">
                        <img :src="imgHostname+cate.thumb" :title="cate.cname" :alt="cate.cname">
                    </div>
                    <div class="product-item" v-for="item in cate.goods" :key="item.gid">
                        <div class="item-img">
                            <img :src="imgHostname+item.gthumb" :title="item.gname" :alt="item.gname">
                        </div>
                        <span>{{item.gname}}</span>
                        <div class="item-label">
                            <span>限时抢购</span>
                        </div>
                        <div class="item-word">
                            <span>￥{{item.mprice}}<del>￥{{item.sale}}</del></span>
                        </div>
                    </div>
                </div>
            </div>
            <div class="mask">释放加载</div>
            <div style="height: 1.28rem"></div>
        </div>
        <my-footer :active="0"></my-footer>
    </div>
        <!-- 商品详情区结束 -->

</template>

<script>
    import MyHeader from '../../components/my-header/my-header';
    import MyFooter from '../../components/my-footer/my-footer';
    import {SUCCESS,HOSTNAME,IMGHOSTNAME} from '../../config/base'
    import axios from 'axios';
    export default {
        name: "index",
        components: {
            MyHeader,
            MyFooter,
        },
        data(){
            return {
                imgHostname:IMGHOSTNAME,
                cateGoods:[
                    //{id:'',cname:'',thumb:'',goods:''}
                ]
            }
        },
        methods:{
            getCateGoods(){
                axios.get(HOSTNAME+'/api/index').then(res=>{
                    res = res.data;
                    if(res.code == SUCCESS){
                        this.cateGoods = res.data;
                    }
                })
            }
        },
        beforeMount() {
            this.getCateGoods();
        }
    }
</script>


<style>
    @import "../../components/my-header/css/index.css";
    html,body{
        height: 100%;
    }
    .van-swipe-item>img{
        width: 100%;
        height: 250px;
    }
</style>